.main {
    display:flex;
    justify-content: center;
    position: relative;
    overflow: hidden;
}

/* Start image styles */
.content{
    &__image{
        height: 775px;
        display: flex;
            .content__left-img{
                width: 238px;
                height: 331px;

                align-items: flex-start;
                
                background-image: url('../../materials/images/image2.png');
                background-repeat: no-repeat;
                background-size: 222%;
                background-position: 50% -50px;
                transform: rotate(1.1deg);
            }
            
            .content__middle-img{
                width: 476px;
                height: 702px;

                align-self: center;

                background-image: url('../../materials/images/image1.png');
                background-repeat: no-repeat;
                background-size: cover;
                background-position: center;
                transform: rotate(1.5deg);
            }
            
            .content__right-img{
                width: 345px;
                height: 238px;

                align-self: flex-end;
                margin-bottom:10px;
                
                background-image: url('../../materials/images/image3.png');
                background-repeat: no-repeat;
                background-size: cover;
                background-position-y: -100px;
                transform: rotate(2.03deg);
            }
    }
}
/* End image styles */


/* Start slider styles */
.content__slider{
    position: absolute;
    top:calc(50% - 100px);
    // left:-100px;
    left:0;
    right:0;
    .content__left-text, .content__middle-text, .content__right-text{
        &:hover:after{
            content:'Drag me';
            position: absolute;
            left:0.1px;
            right: 0.1px;
            animation-name:dragme;
            animation-duration:2s;

            width:100%;
            background-color:#555;
            color:red;
            animation: 3s ease-in forwards dragme;
            animation-iteration-count: 2;
            opacity: 0;
        }

        @keyframes dragme {
            0% {
                opacity: 0;
            }
            50%{
                opacity: 0.9;
            }
            100%{
                opacity: 0;
            }
        }
        font-family: 'Schnyder_Cond_XL_Demi';
        font-size: 125.581px;
        line-height: 86%;

        text-transform: uppercase;
        mix-blend-mode: darken;
        letter-spacing: 10px;
        text-align: center;
    
        color:white;
        text-shadow: #580300 1px 1px 0, #580300 -1px -1px 0,
        #580300 -1px 1px 0, #580300 1px -1px 0;
    }
    
    .content__left-text{
        transform: rotate(-1.5deg);
        // box-sizing: border-box;
    }
    
    .content__middle-text{
        color: #580300;
    }
    
    .content__right-text{
        transform: rotate(1.5deg);
        // box-sizing: border-box;
    }
}
/* End slider styles */

